:root{
  --primary: #efb6b2;
  --secondary: #4e4e4e;
  --error: #ff4a4a;
  /* background: black; */
}


/* base styles & title */
body{
  font-family: 'Orbitron', sans-serif;
  color: var(--secondary);
}
.App{
  max-width: 100%;
  margin: 0 auto;
}
